.box {
    display: flex;
    font-family: 'IBM Plex Mono', monospace;
    position: absolute;
    opacity: 1;

    justify-content: center;
    align-items: center;
    text-align: center;

    margin-right: 10px;
}

.box-content {
    word-wrap: break-word;
    min-width: 0;
    z-index: 2;
    transform: translateZ(2px);
    position: relative;
}

.box-content-extra-type {
    color: grey;
}

.box-empty {
    background: #f4f4f7;
    z-index: -1;
}

.box-full {
    background: #e5e6f1;
}

.box-animated {
    transition-property: opacity, transform;
    transition-duration: 0.5s;
    transition-timing-function: ease;
}

.active-box-selection {
    background: rgba(252, 233, 168);
    width: 40px;
    height: 40px;
    z-index: 2;
    position: absolute;
}

.active-box-selection-1 {
    border: none;
}

.active-box-selection-2 {
    /* border-style: dashed; */
    border: none;
}

.active-box-selection-animated {
    transition-property: opacity, transform;
    transition-timing-function: ease;
}

.box.box-created,
.box.box-removing,
.box.box-removed {
    opacity: 0;
}

